:root {
  --blue: #533392;
  --yellow: #CAC42D;
  --cyan: #00f5ff;
  --orange: #F29B3A;
  --white: #fff;
  --purple: #7552A1;
  --violet: #CA3A8F;
  --black: #111;
}

* {
  font-family: 'Poppins', sans-serif;
}

.navbar {
  background: transparent;
  height: 90px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 1.2rem;
  top: 0;
  z-index: 20;
  width: 100%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  position: fixed;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.15);
}

.nav-container {
  padding: .5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1240px;
}

.nav-logo {
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  flex-grow: 1;
  max-width: 250px;
  height: 80px;
  padding: 0rem;
  position: relative;
}

.nav-logo .img-header {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 45%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  padding: 0rem 0;
  margin-top: 0rem;
}

.nav-links {
  color: black;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-size: 1.05rem;
  border-bottom: 3px solid transparent;
  transition: .3s;
  position: relative;
  padding-left: 0;
}

.nav-links:hover {
  color: #CA3A8F;
  color: var(--violet);
}

.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
  position: relative;
}

.dots {
  padding: 0;
  margin: 0;
  width: 6px;
  height: 6px;
  background: red;
  border-radius: 50%;
  position: absolute;
  top: 6px;
  right: 6px;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
  border-radius: 2rem;
}

.nav-item:hover:after {
  width: 100%;
  background: linear-gradient(45deg, #F29B3A, #7552A1, #CA3A8F);
  background: linear-gradient(45deg, var(--orange), var(--purple), var(--violet));
  color: #CA3A8F;
  color: var(--violet);
}

.nav-item:hover {
  color: #00f5ff;
  color: var(--cyan);
}

.nav-item .active {
  color: #00f5ff;
  color: var(--cyan);
  border: 1px solid #00f5ff;
  border: 1px solid var(--cyan);
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 1000px) {
  .navbar {
    padding: 0 1rem;
    margin: 0;
    background: white;
  }

  .img-header {
    width: 80% !important;
  }

  .nav-container {
    max-width: 320px;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    border-top: 1px solid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #fff;
    background: var(--white);
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    border-top: 3px solid #F29B3A;
    border-top: 3px solid var(--orange);
    border-bottom: 3px solid #F29B3A;
    border-bottom: 3px solid var(--orange);
  }

  .nav-item .active {
    color: #00f5ff;
    color: var(--cyan);
    border: none;
  }

  .nav-links {
    padding: 1rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #111;
    color: var(--black);
  }

  .nav-logo {
    margin-left: 0rem;
  }
}

