.periodic {
    text-shadow: 0 0 0.4vw transparent;
    margin-top: 3rem;
    background: black;
}

.wrapper_1 {
    position: relative;
    overflow: hidden;
    padding: 2%;
}

.wrapper_1>input {
    -webkit-appearance: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}

.periodic-table {
    display: grid;
    grid-gap: 5px;
    grid-template-columns: repeat(18, 1fr);
}

.element {
    position: relative;
    font-size: 0.5vw;
    padding-bottom: 100%;
    cursor: pointer;
    color: #fff;
    transition: 500ms;
}

.element .overlay {
    position: fixed;
    z-index: 1;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    /* background-color: rgba(17, 24, 39, 1); */
    opacity: 0;
    pointer-events: none;
    transition: 500ms;
}

.element .square {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: 2px solid;
    box-sizing: border-box;
    background: rgba(17, 24, 39, 1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition-property: transform, z-index, left, right, top, bottom;
    transition-duration: 100ms, 0ms, 200ms, 200ms, 200ms, 200ms;
    transition-delay: 0ms, 100ms, 0ms, 0ms, 0ms, 0ms;
}

.element .atomic-number {
    position: absolute;
    left: 0;
    top: 0;
    padding: 2px;
}

.element .label {
    text-align: center;
    transition: 200ms;
}

.element .symbol {
    font-size: 1.7vw;
}

.element .name {
    font-size: 0.7vw;
}

.element .atomic-mass {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 2px;
    text-align: center;
}

.element .atomic-weight {
    position: absolute;
    right: 0;
    top: 0;
    list-style: none;
    margin: 0;
    padding: 2px;
    opacity: 0;
    transition: 200ms;
    text-align: right;
}

.element .model {
    display: none;
    position: absolute;
    left: -500%;
    right: -500%;
    top: -500%;
    bottom: -500%;
    transform: scale(0.1);
}

.element .model .orbital {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    border: 5px solid;
    border-radius: 50%;
    opacity: 0.25;
}

.element .model .orbital:nth-child(1) {
    margin: 10%;
    animation-duration: 40s;
}

.element .model .orbital:nth-child(2) {
    margin: 15.5%;
    animation-duration: 34s;
}

.element .model .orbital:nth-child(3) {
    margin: 21%;
    animation-duration: 28s;
}

.element .model .orbital:nth-child(4) {
    margin: 26.5%;
    animation-duration: 22s;
}

.element .model .orbital:nth-child(5) {
    margin: 32%;
    animation-duration: 16s;
}

.element .model .orbital:nth-child(6) {
    margin: 37.5%;
    animation-duration: 10s;
}

.element .model .orbital:nth-child(7) {
    margin: 43%;
    animation-duration: 4s;
}

.element .model .orbital .electron {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.element .model .orbital .electron::before {
    content: "";
    position: absolute;
    left: calc(50% - 0.7vw);
    top: -0.7vw;
    width: 1.4vw;
    height: 1.4vw;
    background-color: currentColor;
    border-radius: 50%;
    opacity: 0.75;
}

